$ns: "fdt-";
@import 'style/global.scss';

.#{$ns}Cart {
    display: flex;
    align-items: center;

    margin-left: scale-w(24);

    text-decoration: none;

    transition: .2s ease-in-out;
    will-change: transform;
    will-change: background-color;

    &:focus,
    &_focused {
        padding: 12px;

        border-radius: 8px;
        background-color: rgba(255, 255, 255, .1);

        transform: scale(1.125);
    }

    &__icon {
        position: relative;

        width: scale-h(40);
        height: scale-h(40);
        margin-right: 18px;

        background-image: url('./cart.svg');
        background-repeat: no-repeat;
        background-size: contain;
    }

    &__badge {
        position: absolute;
        top: -6px;
        right: -6px;

        display: flex;
        justify-content: center;
        align-items: center;

        width: scale-w(24);
        height: scale-w(24);

        font-size: scale-h(14);

        color: #fff;
        border-radius: 50%;
        background: $sc-green;
    }
}
